import request from "@/utils/request";

export function loginWithPhone(params) {
  // :
  // phone: 
  // password: 
  //  :
  // countrycode: 1
  // md5_password: md5, password 
  return request({
    url: "/login/cellphone",
    method: "post",
    params,
  });
}

export function loginWithEmail(params) {
  //  :
  // email: 163 
  // password: 
  //  :
  // md5_password: md5, password 
  return request({
    url: "/login",
    method: "post",
    params,
  });
}

export function loginRefresh() {
  return request({
    url: "/login/refresh",
    method: "post",
  });
}

export function logout() {
  return request({
    url: "/logout",
    method: "post",
  });
}
