import request from "@/utils/request";

export function login(params) {
  //      :
  // phone: 
  // password: 
  //  :
  // countrycode: 1
  // md5_password: md5, password 
  return request({
    url: "/login/cellphone",
    method: "get",
    params,
  });
}

export function userDetail(uid) {
  return request({
    url: "/user/detail",
    method: "get",
    params: {
      uid,
    },
  });
}

export function userPlaylist(params) {
  // limit :  ,  30
  // offset :  ,  :(  -1)*30,  30  limit  ,  0
  return request({
    url: "/user/playlist",
    method: "get",
    params,
  });
}

export function userLikedSongsIDs(uid) {
  return request({
    url: "/likelist",
    method: "get",
    params: {
      uid,
      timestamp: new Date().getTime(),
    },
  });
}
